.title    
    font-weight: 700
    font-size: 48px
    line-height: 60px
    color: $second-color
    margin-bottom: 0
    &_fz14
        font-size: 14px
        line-height: 21px
    &_fz16
        font-size: 16px
        line-height: 24px
        position: relative
        z-index: 2
        padding-left: 10px
        @include round-before
    &_fz20
        padding-left: 43px
        font-size: 20px
        line-height: 30px
        position: relative
        z-index: 2
        @include round-before
        &::before
            width: 15px
            height: 15px
            left: -7px
    &_fz36
        font-size: 36px
        line-height: 60px
    &_black
        color: #000000
    