.tools
    padding-top: 120px
    &__wrapper
        margin-top: 60px
        display: grid
        grid-template: repeat(2, minmax(270px, auto)) / repeat(4, 270px)
        grid-auto-rows: minmax(270px, auto)
        row-gap: 13px
        justify-content: space-around
    &__item 
        padding: 30px 29px 25px 30px
        border: 1px solid #000000
    &__title
        margin-top: 20px
    &__descr
        margin-top: 15px
        margin-bottom: 0
        font-size: 12px
        line-height: 18px
        color: #000000
    &__img
        height: 65px
        line-height: 65px
